.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  text-align: center;
  background-color: #222;
  padding: 8px;
  color: white;
}

.App-title {
  font-size: 15px;
}

.App-intro {
  font-size: large;
}

.Div-border-red{
  border-style: dashed;
  border-width: 2px;
  border-color: red;
}

.Div-border-yellow{
  border-style: dashed;
  border-width: 2px;
  border-color: yellow;
}

.Div-border-blue{
  border-style: dashed;
  border-width: 2px;
  border-color: blue;
}

.Div-border-green{
  border-style: dashed;
  border-width: 2px;
  border-color: green;
}

.Div-side1{
  float:left;
  width:46%;
  min-width: 500px;
}
.Div-side2{
  float:left;
  width:34%;
  min-width: 400px;
}
.Div-side3{
  float:left;
  width:18%;
  min-width: 200px;
}

.Div-scroll{
  height: 300px;
  overflow-y: scroll;
}
.Div-bg-color-orangeish{
  background-color: rgb(255, 140, 100);
}
.Div-bg-color-yellowish{
  background-color: rgb(253, 255, 131);
}
.Div-bg-color-greenish{
  background-color: rgb(119, 255, 111);
}

.rejt-name{
  color: chartreuse;
  font-weight: bold;
}

.rejt-collapsed {
  cursor: pointer;
}

.rejt-value{
  cursor: pointer;
}

.rejt-minus-menu{
  cursor: pointer;
}

.rejt-plus-menu{
  cursor: pointer;
}

.Font-size15px{
  font-size: 15px;
}


